import NormalGame from "../views/NormalGame.vue";
import VuetifyExample from "../views/VuetifyExample.vue";
import VuetifyTest from "../views/VuetifyTest.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "main",
    component: Main
  },
  {
    path: "/normal_game",
    name: "normal_game",
    component: NormalGame
  },
  {
    path: "/survival_game",
    name: "survival_game",
    component: SurvivalGame
  },
  {
    path: "/vuetify_example",
    name: "vuetify_example",
    component: VuetifyExample
  },
  {
    path: "/vuetify_test",
    name: "vuetify_test",
    component: VuetifyTest
  }
  // {
  //   path: "/score",
  //   name: "score",
  //   component: () =>
  //     import(/* webpackChunkName: "about" */ "../views/Score.vue")
  // }
];

const router = new VueRouter({
  routes
});

export default router;
