<template>
  <v-app id="inspire">
    <v-container class="grey lighten-5 _container">
      <v-row class no-gutters>
        <v-col cols="5">
          <SurvivalPlayer :player="players[0]" @score="onScore" />
        </v-col>
        <v-col cols="2">
          <v-card class="pa-2 _timer-panel" outlined tile>타이머</v-card>
        </v-col>
        <v-col cols="5">
          <SurvivalPlayer :player="players[1]" @score="onScore" />
        </v-col>
      </v-row>
      <v-row class no-gutters>
        <v-col cols="5">
          <SurvivalPlayer :player="players[2]" @score="onScore" />
        </v-col>
        <v-col cols="2">
          <v-card class="pa-2 _control-panel" outlined tile>
            <v-btn color="success" dark large @click="nextTurn">턴 넘기기</v-btn>
          </v-card>
        </v-col>
        <v-col cols="5">
          <SurvivalPlayer :player="players[3]" @score="onScore" />
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
// @ is an alias to /src
import SurvivalPlayer from "@/components/score/SurvivalPlayer.vue";

export default {
  name: "score",
  components: {
    SurvivalPlayer
  },
  data: () => ({
    players: [
      {
        live: true,
        turnOn: true,
        name: "이범희",
        score: 20,
        getScore: 0,
        avatarUrl: "",
        ballColor: "yellow",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "허석",
        score: 30,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "이상원",
        score: 30,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      },
      {
        live: true,
        turnOn: false,
        name: "임정인",
        score: 20,
        getScore: 0,
        avatarUrl: "",
        ballColor: "white",
        hr: 0
      }
    ]
  }),
  methods: {
    getLivePlayers() {
      return this.players.filter(player => player.live);
    },
    onScore(name, score) {
      const players = this.getLivePlayers();

      const scoredPlayer = players.filter(player => {
        return player.name === name;
      })[0];

      if (!scoredPlayer.live || !scoredPlayer.turnOn) {
        alert(scoredPlayer.name + "님의 턴이 아닙니다.");
        return;
      }

      const otherPlayers = players.filter(player => {
        return player.name !== name;
      });

      if (otherPlayers.length === 0) return;

      let totalGetScore = 0;
      otherPlayers.forEach(player => {
        let finalScore = score;
        if (player.score < score) {
          finalScore = Math.abs(player.score);
        }

        player.score -= finalScore;
        totalGetScore += finalScore;
      });

      scoredPlayer.score += totalGetScore;

      scoredPlayer.getScore += score;

      otherPlayers.forEach(player => {
        if (player.score === 0) {
          player.live = false;
          player.turnOn = false;
        }
      });
    },
    nextTurn() {
      const players = this.getLivePlayers();
      const idx = players.findIndex(player => player.turnOn);
      const nextIdx = (idx + 1) % players.length;
      players[idx].turnOn = false;
      players[idx].hr += 1;
      players[nextIdx].turnOn = true;

      const preBallColor = players[idx].ballColor;
      const nextBallColor = preBallColor === "yellow" ? "white" : "yellow";
      players.forEach(player => (player.ballColor = preBallColor));
      players[nextIdx].ballColor = nextBallColor;
    }
  }
};
</script>

<style scoped>
._container {
  margin: 0;
  padding: 0;
}
._timer-panel,
._control-panel {
  height: 50vh;
}

._user-panel {
  height: 50vh;
}
._current-score {
  position: relative;
  font-size: 40pt;
}
</style>
